/* ==============================================
		 5. Home
	===============================================*/

#home {
  .hero {
    position: relative;
    padding: 29vh 0 17vh;
    display: flex;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
    margin: 0;



    h2 {
      font-weight: 300;
      font-size: 34px;
      margin-bottom: 40px;
      line-height: 2;
    }

    h3 {
			margin-top: 36px;
			font-size: 20px;
      color: $primary;
    }

    p {
      font-size: 16px;
      font-weight: 300;
      padding: 0px;
    }

    .flipper {
      display: block;
      margin: 0 auto;
      perspective: 1000;
      @include clearfix;

      &:hover .flipper-back {
        transform: rotateY(0deg);
      }

      &:hover .flipper-front {
        transform: rotateY(-180deg);
      }

      .flipper-content {
        transform: perspective(1000px);
        transform-style: preserve-3d;
        position: relative;
      }

      .flipper-front,
      .flipper-back {
        position: absolute;
        top: 0;
        left: 0;
        text-align: center;
        backface-visibility: hidden;
        transition: 0.6s;
        transform-style: preserve-3d;
      }

      .flipper-front {
        z-index: 2;
      }

      .flipper-back {
        transform: rotateY(180deg);
      }
    }

    .avatar {
      width: 80%;
    }

    @media (min-width: 992px) {}

    @media (max-width: 991px) {
      padding: 15vh 0;
      text-align: center;

      h2 {
        font-size: 24px;
      }

      h3 {
        font-size: 24px;
      }

      .flipper {
        min-height: 205px;
      }

      .avatar {
        width: 50%;
      }

      p {
        font-size: 14px;
      }

    }
  }

  .particles-layout {
    background: #0052e9;
  }
}

/* ==============================================
		 6. Portfolio
	===============================================*/

.portfolio {
  h3 {
    font-size: 1.6em;
    font-weight: 700;

    &:not(:first-child) {
      margin-top: 50px;
    }

    @media (max-width: 991px) {
      font-size: 1em;
    }
  }

  h4 {
    font-size: 1.1em;
    margin-bottom: 20px;

    &:not(:first-child) {
      margin-top: 50px;
    }
  }

  h5 {
    margin-top: 30px;
  }

  .hero {
    padding: 0;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover; // background-attachment: fixed;
    height: 50vh;
  }

  .title-block {
    margin-bottom: 50px;
    padding-bottom: 50px;

    @media(max-widh: 991px) {
      margin-bottom: 10px;
    }
  }

  .img-fluid {
    margin: 30px 0;
  }

  .card {
    position: relative;
    opacity: 0.8;
    transition: 0.3s all;
    border: none;
    margin-bottom: 40px;
    color: #676767;

    &:hover {
      transition: 0.3s all;
      opacity: 1;
      box-shadow: 0px 22px 43px rgba(68, 68, 68, 0.2);
      margin-top: -5px;

      .card-body {
        transition: 0.3s all;
        padding-left: 1.25rem;
      }
    }

    .card-body {
      transition: 0.3s all;
      position: relative;
      padding-left: 0;
    }

    .card-title {
      color: rgba(0, 0, 0, 0.87);
      display: inline-block;
    }

    .card-title:last-child {
      margin-bottom: 0;
    }

    @media (min-width: 992px) {
      .card-title {
        font-size: 28px;
        letter-spacing: -0.5px;
      }
    }

    @media (max-width: 991px) {
      margin-bottom: 10px;

      .card-body {
        padding: 10px 0 0 0;
      }

      .card-title {
        font-size: 0.8em;
      }

      p {
        font-size: 0.8em;
      }
    }
  }

  &.dls {
    svg {
      height: 54px;
    }
  }
}

.portfolio-footer-link {
  color: #ccc;
  margin: 8px 12px;
  word-break: keep-all;
  display: inline-block;
  font-size: 1.2em;
}

